import { JSX } from "solid-js";

export default function InputWithCopy(
  props: JSX.InputHTMLAttributes<HTMLInputElement>
) {
  let input: any;

  const copy = () => {
    input.select();
    navigator.clipboard.writeText(input.value);
  };

  return (
    <div class="flex flex-col rounded-md shadow relative w-[10em] m-1">
      <input
        type="text"
        readOnly={props.readOnly}
        maxLength="11"
        value={props.value}
        ref={input}
        onFocus={() => input.select()}
        {...props}
        class={`py-1 px-2 w-full border-gray-200 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500 transition-all font-mono ${
          props?.class ?? ""
        }`}
      />
      <button
        type="button"
        onClick={copy}
        class="group peer absolute right-0 top-0 bottom-0 justify-center items-center w-[2em] rounded-r-md font-bold bg-blue-500 text-white hover:bg-blue-600 "
      >
        <span class="inline-block relative before:content-['ðŸ—‹'] before:absolute mr-[0.25em] before:bottom-[0.3em] before:-right-[0.3em] before:bg-blue-500 group-hover:before:bg-blue-600 before:leading-[1em]">
          ðŸ—‹
        </span>
      </button>
      <div class="bg-black bg-opacity-60 rounded-lg inline-block p-1 text-white absolute bottom-full mb-1 peer-focus:visible invisible transition-all duration-1000 ease-out">
        Copied!
      </div>
    </div>
  );
}
